<template>
  <v-container fluid fill-height style="padding:0;" id="nodeiterator" grid-list-md>
    <Blog :items="items" :time="time" :title="title">
      <div class="blogcontent">
        <v-layout row wrap>
          <v-flex xs12>
            <v-hover>
              <v-card
                slot-scope="{ hover }"
                :class="`elevation-${hover ? 12 : 2}`"
                min-height="200"
              >
                <v-card-title>
                  <h3 id="node">创建NodeIterator遍历</h3>
                </v-card-title>
                <v-divider></v-divider>
                <v-card-text>
                    <v-layout row wrap>
                        <v-flex xs12>
                            <pre class="code">{{pres[0]}}</pre>
                        </v-flex>
                    </v-layout>
                </v-card-text>
              </v-card>
            </v-hover>
          </v-flex>

          <v-flex xs12>
            <v-hover>
              <v-card
                slot-scope="{ hover }"
                :class="`elevation-${hover ? 12 : 2}`"
                min-height="200"
              >
                <v-card-title>
                  <h3 id="walker">创建TreeWalker遍历</h3>
                </v-card-title>
                <v-divider></v-divider>
                <v-card-text>
                    <v-layout row wrap>
                        <v-flex xs12>
                            <pre class="code">{{pres[1]}}</pre>
                        </v-flex>
                    </v-layout>
                </v-card-text>
              </v-card>
            </v-hover>
          </v-flex>
        </v-layout>
      </div>
    </Blog>
  </v-container>
</template>

<script>
import Blog from "../Blog.vue";
export default {
  components: { Blog },
  data() {
    return {
      time: "2019-3-5",
      title: "Dom节点遍历器",
      items: [
        {
          title: "创建NodeIterator遍历",
          target: "#node"
        },
        { divider: true },
        {
          title: "创建TreeWalker遍历",
          target: "#walker"
        },
        { divider: true }
      ],
      pres: 
[
`
    // 下面这段代码实现功能是过滤出所有的p

    const box = document.querySelector('#box');
    const ci = document.createNodeIterator(
        box, // 根节点
        NodeFilter.SHOW_ELEMENT + NodeFilter.SHOW_COMMENT, // 表示什么类型的节点将被筛选出来，也就是Iterator初始节点集合
        /**
         * NodeFilter包含的其他属性如下
         * 
         * 1. SHOW_ALL 所有节点
         * 2. SHOW_COMMENT 所有注释节点
         * 3. SHOW_DOCUMENT 所有document节点
         * 4. SHOW_ELEMENT 所有元素节点
         * 5. SHOW_TEXT 所有文本节点
        */
        { 
            acceptNode: function(node) { // 定义过滤函数，会在初始集合中的每一个节点调用
                if(node.nodeName === 'P'){
                    return NodeFilter.FILTER_ACCEPT; // 接受此节点
                } else if (node.hasChildNodes()) { // 如果此节点有孩子节点
                    return NodeFilter.FILTER_SKIP; // 过滤此节点本身，但是其子节点不过滤
                } else {
                    return NodeFilter.FILTER_REJECT; // 过滤掉这个节点和它的子节点
                }
            } 
        }
    );

    let node = ci.nextNode(); // 下一个节点
    while(node) {
        console.log(node);
        node = ci.nextNode();
    }

    // ci.previousNode(); 上一个节点
`,
`
    // TreeWalker的作用和使用方式和NodeIterator几乎一模一样，只不过TreeWalker提供了一些更细粒度的节点访问方法

    const box = document.querySelector('#box');
    const treeWalker = document.createTreeWalker(
        box, // 根节点
        NodeFilter.SHOW_ELEMENT,
        { 
            acceptNode: function(node) { 
                return NodeFilter.FILTER_ACCEPT;
            } 
        }
    );

    // 遍历每一个节点
    let node = treeWalker.nextNode();
    while(node) {
        console.log(treeWalker.currentNode);
        node = treeWalker.nextNode();
    }

    // 调用这些方法都会移动遍历指针到当前位置
    // treeWalker.firstChild(); 第一个孩子节点
    // treeWalker.lastChild(); 最后一个孩子节点
    // treeWalker.nextSibling(); 下一个兄弟节点
    // treeWalker.parentNode(); 父节点
    // treeWalker.nextNode(); 下一个节点
    // treeWalker.previousNode(); 上一个节点
    // treeWalker.previousSibling(); 上一个兄弟节点
`
]
    };
  },
  mounted() {

    
  },
  methods: {}
};
</script>

